<template>
  <div class="section">
    <section class="container section-spacing">
      <div class="columns is-centered">
        <div class="column is-8">
          <h1 class="title is-1">{{ event.pagePartnerRegistrationTitle }}</h1>
          <h2 class="subtitle is-4">
            {{ event.pagePartnerRegistrationSubtitle }}
          </h2>
        </div>
      </div>
    </section>

    <section class="container section-spacing">
      <div class="columns is-centered content">
        <div class="column is-8">
          <Markdown :input="substitutedMarkdownContent" />
        </div>
      </div>
    </section>

    <section class="container">
      <div class="columns is-centered">
        <div class="column is-6">
          <form @submit.prevent="register">
            <h2 class="title has-text-poppins is-5 has-red-stripe">
              let's first confirm a few details about you
            </h2>

            <b-field
              label="First Name"
              :type="{ 'is-danger': $v.personAnswers.firstName.$error }"
              :message="
                $v.personAnswers.firstName.$error
                  ? 'Please enter your first name'
                  : ''
              "
            >
              <b-input
                v-model="personAnswers.firstName"
                placeholder="Leslie"
                @blur="$v.personAnswers.firstName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Last Name"
              :type="{ 'is-danger': $v.personAnswers.lastName.$error }"
              :message="
                $v.personAnswers.lastName.$error
                  ? 'Please enter your last name'
                  : ''
              "
            >
              <b-input
                v-model="personAnswers.lastName"
                placeholder="Miles"
                @blur="$v.personAnswers.lastName.$touch()"
                type="text"
                icon="account"
              >
              </b-input>
            </b-field>

            <b-field
              label="Email"
              :type="{
                'is-danger': $v.personAnswers.email.$error || errors.email
              }"
              :message="errors.email ? errors.email : ''"
            >
              <b-input
                v-model="personAnswers.email"
                placeholder="francis@example.com"
                @blur="$v.personAnswers.email.$touch()"
                type="email"
                icon="email"
              >
              </b-input>
            </b-field>

            <fieldset>
              <legend class="label">
                Do you have any Dietary Requirements?
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in dietaryRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="personAnswers.data.dietaryRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <fieldset>
              <legend class="label">
                Do you have any Accessability Requirements?
              </legend>
              <div class="checkbox-row">
                <b-checkbox
                  v-for="requirement in accessabilityRequirements"
                  :key="requirement"
                  class="checkbox-column"
                  v-model="personAnswers.data.accessabilityRequirements"
                  :native-value="requirement"
                >
                  {{ requirement }}
                </b-checkbox>
              </div>
            </fieldset>

            <input
              class="button is-medium is-pulled-right is-primary"
              type="submit"
              value="Register"
            />
          </form>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import TicketService from "@/services/TicketService";
import ProfileService from "@/services/ProfileService";
import NProgress from "nprogress";
import axios from "axios";
import { required, email } from "vuelidate/lib/validators";
import Markdown from "@/components/Markdown";
import { markdownSubstitutionsMixin } from "@/mixins/markdownSubstitutionsMixin";

export default {
  name: "PartnerRegistration",
  components: {
    Markdown
  },
  validations: {
    personAnswers: {
      firstName: { required },
      lastName: { required },
      email: { required, email }
    }
  },
  mixins: [markdownSubstitutionsMixin],
  data() {
    return {
      personAnswers: this.person,
      substitutedMarkdownContent: this.substituteMarkdownContent(
        this.event.pagePartnerRegistrationContentMarkdown,
        this.person.firstName,
        this.person.lastName
      ),
      errors: {}
    };
  },
  props: {
    event: {
      type: Object,
      required: true
    },
    person: {
      type: Object,
      required: true
    },
    ticketOffer: {
      type: Object,
      required: true
    }
  },
  methods: {
    register() {
      this.$v.$touch();
      if (!this.$v.$invalid) {
        NProgress.start();

        axios
          .all([
            ProfileService.updateProfile(this.person.id, {
              person: {
                firstName: this.personAnswers.firstName,
                lastName: this.personAnswers.lastName,
                email: this.personAnswers.email
              },
              profile: this.personAnswers.data
            }),
            TicketService.registerPartner(this.ticketOffer.id)
          ])
          .then(
            axios.spread(() => {
              NProgress.done();
              this.$snackbar.open({
                message: `Successfully Registered: Thank you`,
                position: "is-bottom-left"
              });
            })
          )
          .catch(error => {
            console.log(error);
          });
      }
    }
  },
  computed: {
    dietaryRequirements() {
      return this.$store.state.dietaryRequirements;
    },
    accessabilityRequirements() {
      return this.$store.state.accessabilityRequirements;
    }
  }
};
</script>

<style scoped>
.rsvp-radios {
  margin: 1rem;
}
</style>
